<template>
  <header id="page-topbar">
    <div class="navbar-header">
      <div class="d-flex">
        <!-- LOGO -->
        <div class="navbar-brand-box">
          <router-link to="/" class="logo logo-dark">
            <span class="logo-sm">
              <img src="@/assets/images/logo.png" alt height="35" />
            </span>
            <span class="logo-lg">
              <img src="@/assets/images/logo.png" alt height="17" />
            </span>
          </router-link>
          <router-link to="/" class="logo logo-light">
            <span class="logo-sm">
              <img src="@/assets/images/logo.png" alt height="35" />
            </span>
            <span class="logo-lg">
              <img src="@/assets/images/logo.png" alt height="60" />
            </span>
          </router-link>
        </div>
        <button
          id="vertical-menu-btn"
          type="button"
          class="btn btn-sm px-3 font-size-16 header-item"
          @click="toggleMenu"
        >
          <i class="fa fa-fw fa-bars"></i>
        </button>
      </div>
      <div class="d-flex">
        <div class="dropdown d-none d-lg-inline-block ms-1">
          <button
            type="button"
            class="btn header-item noti-icon"
            @click="initFullScreen"
          >
            <i class="bx bx-fullscreen"></i>
          </button>
        </div>
        <div v-if="can('view_store')" class="dropdown d-none d-lg-inline-block ms-1">
          <button type="button" class="btn header-item noti-icon waves-effect">
            <router-link to="/lista/tiendas">
              <i class="mdi mdi-cached"></i>
            </router-link>
          </button>
        </div>
        <b-dropdown
          right
          menu-class="dropdown-menu-lg p-0 dropdown-menu-end"
          toggle-class="header-item noti-icon"
          variant="black"
        >
          <template v-slot:button-content>
            <i class="bx bx-bell" :class="{'bx-tada': countNotifications > 0}"></i>
            <span class="badge bg-danger rounded-pill" v-if="countNotifications > 0">
              {{ countNotifications }}
            </span>
          </template>

          <div class="p-3">
            <div class="row align-items-center">
              <div class="col">
                <h6 class="m-0">
                  Notificaciones
                </h6>
              </div>
            </div>
          </div>
          <simplebar style="max-height: 70vh;">
            <div 
              v-for="(notification, index) of listNotifications" :key="index"
              :class="{ notreadnotification: !notification.read}"
            >
              <a 
                href="javascript: void(0);" 
                class="text-reset notification-item"
                @click="readNotification(notification.id, notification.recipient)"
                v-b-modal.readNotification
              >
                <div class="d-flex">
                  <div class="avatar-xs me-3">
                    <span
                      class="avatar-title bg-primary rounded-circle font-size-16"
                    >
                      <i class="bx bx-cart"></i>
                    </span>
                  </div>
                  <div class="flex-grow-1">
                    <h6 class="mt-0 mb-1">
                      {{ notification.title }}
                    </h6>
                    <div class="font-size-12 text-muted">
                      <p class="mb-1">
                        {{ notification.subtitle }}
                      </p>
                      <p class="mb-0">
                        <i class="mdi mdi-clock-outline"></i>
                        {{ notification.created }}
                      </p>
                    </div>
                  </div>
                </div>
              </a>
            </div>
          </simplebar>
          <template v-if="hasNextPage">
            <div class="p-2 border-top d-grid">
              <a
                class="btn btn-sm btn-link font-size-14 text-center"
                href="javascript:void(0)"
                @click="getNotifications"
              >
                <i class="mdi mdi-arrow-right-circle me-1"></i>
                Ver más
              </a>
            </div>
          </template>
        </b-dropdown>

        <b-dropdown
          right
          variant="black"
          toggle-class="header-item"
          menu-class="dropdown-menu-end"
        >
          <template v-slot:button-content>
            <img
              class="rounded-circle header-profile-user"
              src="@/assets/images/LogoF.png"
              alt="Header Avatar"
            />
              <span class="d-none d-xl-inline-block ms-1">
                <div v-if="loggedIn">
                  {{ getFullName }}
                </div>
              </span
            >
            <i class="mdi mdi-chevron-down d-none d-xl-inline-block"></i>
          </template>
          <button @click="logoutUser" class="dropdown-item text-danger">
            <i
              class="bx bx-power-off font-size-16 align-middle me-1 text-danger"
            ></i>
            {{ $t("navbar.dropdown.henry.list.logout") }}
          </button>
        </b-dropdown>
      </div>
    </div>
    <b-modal 
      v-if="selectedNotification"
      id="readNotification" 
      :title="selectedNotification.title"
      cancel-title="Cancelar"
      ok-title="Aceptar"
    >
      {{ selectedNotification.text }}
      <div v-for="(product, index) of selectedNotification.products" :key="index">
        <strong>{{ product.product }}:</strong> {{ product.stock }}
      </div>
    </b-modal>
  </header>
</template>

<script>
import simplebar from "simplebar-vue";
import i18n from "../i18n";
import { authComputed, 
authenticationMethods,
socketComputed } from "@/state/helpers";
import { REVOKE_TOKEN } from '@/api/graphql/mutations/auth'
import { ALL_NOTIFICATIONS } from '@/api/graphql/query/notifications'
import { mapMutations } from 'vuex'
import { decode_bs64, formatNotification } from '@/utils/general'
import permissionMixin from "@/mixins/permissionMixin";

export default {
  mixins: [
    permissionMixin
  ],
  data() {
    return {
      languages: [
        {
          flag: require("@/assets/images/flags/us.jpg"),
          language: "en",
          title: "English",
        },
        {
          flag: require("@/assets/images/flags/french.jpg"),
          language: "fr",
          title: "French",
        },
        {
          flag: require("@/assets/images/flags/spain.jpg"),
          language: "es",
          title: "Spanish",
        },
        {
          flag: require("@/assets/images/flags/chaina.png"),
          language: "zh",
          title: "Chinese",
        },
        {
          flag: require("@/assets/images/flags/arabic.png"),
          language: "ar",
          title: "Arabic",
        },
      ],
      lan: i18n.locale,
      text: null,
      flag: null,
      value: null,
      myVar: 1,
      selectedNotification: null,
      hasNextPage: false,
      lastCursor: null
    };
  },
  components: { simplebar },
  computed: {
    ...authComputed,
    ...socketComputed
  },
  methods: {
    ...authenticationMethods,
    ...mapMutations('socketNotifications',['SET_NOTIFICATIONS', 'READ_NOTIFICATION']),
    scrollHandler(event){
    },
    toggleMenu() {
      this.$parent.toggleMenu();
    },
    toggleRightSidebar() {
      this.$parent.toggleRightSidebar();
    },
    initFullScreen() {
      document.body.classList.toggle("fullscreen-enable");
      if (
        !document.fullscreenElement &&
        /* alternative standard method */ !document.mozFullScreenElement &&
        !document.webkitFullscreenElement
      ) {
        // current working methods
        if (document.documentElement.requestFullscreen) {
          document.documentElement.requestFullscreen();
        } else if (document.documentElement.mozRequestFullScreen) {
          document.documentElement.mozRequestFullScreen();
        } else if (document.documentElement.webkitRequestFullscreen) {
          document.documentElement.webkitRequestFullscreen(
            Element.ALLOW_KEYBOARD_INPUT
          );
        }
      } else {
        if (document.cancelFullScreen) {
          document.cancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
      }
    },
    setLanguage(locale, country, flag) {
      this.lan = locale;
      this.text = country;
      this.flag = flag;
      i18n.locale = locale;
    },
    async logoutUser() {
      const currentUser = JSON.parse(localStorage.getItem('currentUser'))
      const variables = {
        refreshToken: currentUser.refreshToken
      }
      try {
        const response = await this.$apollo.mutate({
          mutation: REVOKE_TOKEN,
          variables: variables
        })
        this.logout()
        this.$router.go('/app-fulfy/')
      } catch (error) {
        console.log(error)
      }
    },
    async getNotifications(){
      try {
        const response = await this.$apollo.query({
          query: ALL_NOTIFICATIONS,
          variables: {
            cursor: this.lastCursor
          }
        }) 
        const { notifications } = response.data
        if(notifications !== null){
          this.hasNextPage = notifications.pageInfo.hasNextPage
          const { edges } = notifications
          const formatData = formatNotification(edges)
          this.lastCursor = formatData.lastCursor
          this.SET_NOTIFICATIONS({data: formatData.data, type: 'query'})
        }
      } catch (error) {
        
      }
    },
    async readNotification(notification_id, recipient_id){
      let idInt = decode_bs64( notification_id ).split(':')[1]
      if (this.hasSocket){
        const socket = this.getSocket
        const data_socket = {
          'type': 'read_notification',
          'notification_id': idInt,
          'recipient': recipient_id
        }
        socket.send(JSON.stringify(data_socket))
      }
      this.READ_NOTIFICATION(notification_id)
      const indexNotification = this.listNotifications.findIndex(notify => notify.id === notification_id)
      this.selectedNotification = this.listNotifications[indexNotification]
    }
  },
   mounted() {
    this.value = this.languages.find((x) => x.language === i18n.locale);
    this.text = this.value.title;
    this.flag = this.value.flag;
    this.getNotifications()
  },
};
</script>
<style scoped>
.notreadnotification{
  background-color: #F2F3F4;
}
</style>