import gql from 'graphql-tag'

const ALL_NOTIFICATIONS = gql`query notifications($cursor: String){
    notifications(after: $cursor){
        pageInfo{
            hasNextPage
        }
        edges{
            cursor
            node{
                id
                createDate
                extraData
                isRead
                recipient{
                    id
                }
            }
        }
      
    }
}`

export {
    ALL_NOTIFICATIONS
}